<template>
  <div class="lcz-blog-header container">
    <div class="title-logo">
      SAKURA
    </div>
    <div class="menu">
      <el-menu
        router
        :default-active="activePath"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect">
        <el-menu-item index="/lczBlogMain/index">首页</el-menu-item>
        <el-menu-item index="/lczBlogMain/code">编码技巧</el-menu-item>
        <el-menu-item index="/lczBlogMain/article">文章</el-menu-item>
        <el-menu-item index="/lczBlogMain/notes">随笔</el-menu-item>
        <el-menu-item index="/lczBlogMain/message">给我留言</el-menu-item>
        <el-menu-item index="/lczBlogMain/upper">关于博主</el-menu-item>
      </el-menu>
    </div>
    <div class="login-area">
        <div class="user-avatar">
          <el-avatar shape="square" size="large" src="https://avatars.githubusercontent.com/u/25954140?s=48&v=4"></el-avatar>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'lczBlogHeader',
  data () {
    return {
      activePath: '/lczBlogMain/index'
    }
  },
  mounted () {
    /* 默认选择一个首页路由定位 */
    if (this.$route.path.indexOf('lczBlogMain/') < 0) {
      this.$router.push({path: '/lczBlogMain/index'})
    }
  },
  methods: {
    handleSelect (val) {
      this.activePath = val
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  display: flex;
  justify-content: center;
}
.title-logo {
  line-height: 60px;
  color: red;
  font-weight: 700;
  letter-spacing: 2px;
  margin-right: 1rem;
}
.lcz-blog-header {
  .menu {
    ul {
      list-style: none;
    }
  }
}
.user-avatar {
  height: 100%;
  display: flex;
  align-items: center;
  margin-left: 1rem;
}

</style>



